@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
#root {
    height: 100vh;
}

body {
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-family: "Nunito Sans", sans-serif;
    background-color: rgb(122, 121, 121);
}

*::-webkit-scrollbar {
    width: 10px;
  }

  *::-webkit-scrollbar-track {
    background: rgb(114, 110, 110);
    border-radius: 8px;
  }
  
  *::-webkit-scrollbar-thumb {
    background-color: rgb(13, 14, 14);
    border-radius: 20px;
  }

.home_page {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    /* margin-bottom: 150px; */
}

.chat_img {
    height: 150px;
    width: 150px;
    border: 1px solid rgb(168, 167, 167);
    padding: 20px;
    border-radius: 8px;
}

.navbar {
    top: 0;
    position: fixed;
    width: 100%;
    background-color: rgb(119, 102, 102);
}

.navbar a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.user_entrance {
    float: right;
    margin-right: 10px;
}

.logout_button {
    background-color:black;
    border-style: none;
    border-radius: 30px;
    width: 70px;
    height: 30px;
    margin-top: 10px;
    margin-left: 10px;
    color:white;
    cursor: pointer;
}

.welcome {
    margin-top: 80px;
    text-transform: capitalize;
}
.home_button {
    background-color:black;
    border-style: none;
    border-radius: 30px;
    width: 70px;
    height: 30px;
    margin-top: 10px;
    margin-left: 10px;
    color:white;
    cursor: pointer;
}
.login_form {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 1px solid black;
    padding: 20px;
    border-radius: 8px;
    background-color: black;
    color: #fff;
}

.form_item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 300px;
    margin-top: 10px;
}

.form_item input {
    outline-style: none;
    border-style: none;
    border-radius: 3px;
    height: 22px;
}

.login_form button {
    background: rgb(29, 161, 242);
    border-radius: 10px;
    outline-style: none;
    border-style: none;
    cursor: pointer;
    margin-top: 10px;
    height: 22px;
    color: white;
}
.contacts_component {
    width: 270px;
    transition: width 0.3s;
    height: 100%;
    overflow-y: scroll;
}

.header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid rgba(189, 189, 192, 0.1);
    height: 40px;
    font-weight: bold;
    color: #5d86c4;;
}

.message_div {
    padding-top: 10px;
    padding-bottom: 10px;
    border: 1px solid black;
    color: rgb(189, 185, 179);
    cursor: pointer;
    -webkit-columns: 2;
            columns: 2;
}

.time_sent {
    font-size: 0.6rem;
}
.username {
    font-size: 0.7rem;
    margin-left: 5px;
}

.message {
    word-break: keep-all;
    font-size: 0.7rem;
    font-family:Arial, Helvetica, sans-serif;
    overflow-wrap: break-word;
}
.chats {
    -webkit-flex: 1 1;
            flex: 1 1;
    width: 85%; 
    margin: 0 auto;
    overflow-y: scroll;
}

#username {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: bisque;
}

.Chat {
    margin: 1rem 0;
    color: #fff;
    padding: 1rem;
    background: linear-gradient(90deg, #1986d8, #7b9cc2);
    max-width: 90%;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .Chat.is-user-msg {
    margin-left: 30%;
    background: linear-gradient(90deg, #413f3f, #6b6d70);
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }

  @media (min-width: 576px) {
  .Chat {
    max-width: 60%;
  }
}
.header_main {
    color: #fff;
}

#username {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.message_form {
    width: 80%;
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    margin-bottom: 9px;
}

.message_input {
    width: 50%;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border: 0;
    border-radius: 10px;
    font-size: 1rem;
    outline: 0;
}
.chat_window {
    height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
    display: -webkit-flex;
    display: flex;
    width: 95%;
}
.main {
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 100%;   
    overflow-y: scroll;
}
.main_chat {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    background-color: rgb(46,46,46)
}

.add_contacts_div {
    bottom: 10px;
    left: 10px;
    position: fixed;
    cursor: pointer;
}
.main_div {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 1px solid black;
    border-radius: 8px;
    background-color: black;
    color: #fff;
    width: 350px;
    height: 450px;
    overflow-y: scroll;
    opacity: 0.8;
}

.select_contact_div {
    display: -webkit-flex;
    display: flex;
    cursor: pointer;
}

.username {
    padding-left: 10px;
    font-size: 0.9rem;
}

.status {
    font-size: 0.7rem;
